$orange: #CC9543;
$text-gray: #b3b3b3;

@mixin column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.App {
  @include column-center;
  padding: 20px 0;
  color: $orange;
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  box-shadow: 5px 6px 10px 3px rgba(0, 0, 0, 0.25);
  margin: 40px auto;
  max-width: 600px;
}

h1 {
  font-size: 40px;
}

.header {
  @include column-center;
  
  .logo {
    width: max-content;
  }
  .title {
    font-weight: bold;
  }
}

form {
  color: black;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  row-gap: 40px;
  font-size: 18px;
  padding: 40px 0;
}
.sub-form {
  color: black;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  row-gap: 20px;
  font-size: 18px;
  padding: 20px 0;
}


.long-input {
  grid-column: 1/3;
}
.single-input {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
  box-sizing: border-box;
  width: 100%;
  padding: 0 20px;

  label, span, select {
    color: $text-gray;
  }
  input, select, .input-mock {
    border: none;
    box-shadow: 0 1px 4px rgba(0,0,0,0.25);
    width: 100%;
    box-sizing: border-box;
    height: 30px;
    font-size: 16px;
    padding: 0 5px;
  }
  .input-mock {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: $text-gray;
    padding: 0 10px;
    gap: 5px;
    cursor: pointer;
    //makes text unseletable since it is supposed to act like a button
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
}
.send {
  width: 120px;
  height: 40px;
  background-color: $orange;
  color: white;
  border: none;
  font-size: 18px;
  font-weight: bold;
  border-radius: 100px;
  cursor: pointer;
}
.cancel-button {
  background-color: rgb(167, 56, 56);
}
