

.education-container {
  width: 100%;
  box-sizing: border-box;
  padding: 20px 10px;
  color: #b3b3b3;
}

.education-card {
  padding: 20px;
  margin: 10px 0 0 0;
  box-shadow: 2px 3px 10px 1px rgba(0, 0, 0, 0.20);
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;

  .years-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
